// src/components/AssetForm.js

import React, { useState } from "react";
import {
  Container,
  Card,
  CardContent,
  Typography,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  Button,
  Stack,
  Box,
} from "@mui/material";
import WebcamCapture from "./WebcamCapture";

//  ""
const locationOptions = [
  "",
  "No. 62 ",
  "No. 62  1",
  "No. 62  2",
  "No. 62  3",
  "No. 62  6",
  "No. 64  1",
  "No. 64  2",
  "No. 64  3",
  "No. 64  4",
  "No. 64  5",
  "No.62  1 ",
  "No.62  2 ",
  "No.62  3 ",
  "No.64  3 ",
  "No.51",
];

// 
const approverOptions = [
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  "  ()",
  // ... () ...
];

//  "Brand"
const brandOptions = [
  "ACER",
  "APPLE",
  "ASUS",
  "DELL",
  "HP",
  "LG",
  "LENOVO",
  "MICORSOFT SURFACE",
  "NIKON",
  "OPPO",
  "SAMSUNG",
  "SHARP",
  "SONY",
  "VNJO Q",
  " ",
];

//  ""
const assetNameOptions = [
  "LAPTOP",
  "DESKTOP - UPC",
  "DESKTOP - MONITOR",
  "DESKTOP ALL IN ONE",
  "MONITOR",
  "UPS",
  "IPAD/TAPLET",
  "",
  "",
  "",
  "",
  "",
  " ",
];

function AssetForm() {
  //  Apps Script ( doPost )
  const [formData, setFormData] = useState({
    usageType: "", // B
    location: "", // C
    approver: "", // D
    assetName: "", // E
    assetCategory: "", // F
    brand: "", // G
    modelType: "", // H
    serialNumber: "", // I
    assetId: "", // J
    barcode: "", // K
    receiveDate: "", // L
    assetStatus: "", // M
    imageBase64: "", // -> N (Direct Link)
    otherDetail: "", // O
    remark: "", // P
  });

  const [showWebcam, setShowWebcam] = useState(false);

  // handleChange  field 
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  //  ->  base64
  const handleImageChange = (e) => {
    const file = e.target.files?.[0];
    if (!file) return;
    const reader = new FileReader();
    reader.onloadend = () => {
      setFormData((prev) => ({ ...prev, imageBase64: reader.result }));
    };
    reader.readAsDataURL(file);
  };

  //  Webcam
  const handleCapture = (imageSrc) => {
    setFormData((prev) => ({ ...prev, imageBase64: imageSrc }));
    setShowWebcam(false);
  };
  const handleCancelWebcam = () => setShowWebcam(false);

  //  Apps Script
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      // URL Web App ( Deploy)
      const scriptURL = "http://localhost:3210/submit"; // <-- 

      const response = await fetch(scriptURL, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      });
      const text = await response.text();

      if (response.ok && text === "SUCCESS") {
        alert("!");

        // 
        setFormData({
          usageType: "",
          location: "",
          approver: "",
          assetName: "",
          assetCategory: "",
          brand: "",
          modelType: "",
          serialNumber: "",
          assetId: "",
          barcode: "",
          receiveDate: "",
          assetStatus: "",
          imageBase64: "",
          otherDetail: "",
          remark: "",
        });
      } else {
        alert(": " + text);
      }
    } catch (error) {
      console.error("Error:", error);
      alert("");
    }
  };

  return (
    <Container maxWidth='md' sx={{ mt: 4 }}>
      <Card>
        <CardContent>
          {/*  */}
          <Box sx={{ textAlign: "center", mb: 2 }}>
            <img
              src='/company_logo.png'
              alt='Company Logo'
              style={{ maxHeight: "90px" }}
            />
          </Box>

          <Typography variant='h5' gutterBottom>
                
          </Typography>

          <Box component='form' onSubmit={handleSubmit} sx={{ mt: 2 }}>
            <Grid container spacing={2}>
              {/*  (B) */}
              <Grid item xs={12} sm={6}>
                <FormControl fullWidth>
                  <InputLabel></InputLabel>
                  <Select
                    name='usageType'
                    value={formData.usageType}
                    label=''
                    onChange={handleChange}
                    required
                  >
                    <MenuItem value=''>--  --</MenuItem>
                    <MenuItem value=''></MenuItem>
                    <MenuItem value='()'>
                      ()
                    </MenuItem>
                    <MenuItem value='Stock'>Stock</MenuItem>
                    <MenuItem value=' ACCOUNTING'>
                       ACCOUNTING
                    </MenuItem>
                    <MenuItem value=' HR'> HR</MenuItem>
                    <MenuItem value=' PURCHASING'>
                       PURCHASING
                    </MenuItem>
                    <MenuItem value=' DATA'> DATA</MenuItem>
                    <MenuItem value=' BRANDING'>
                       BRANDING
                    </MenuItem>
                    <MenuItem value=' MKT COMMU'>
                       MKT COMMU
                    </MenuItem>
                    <MenuItem value=' OFFLINE TRADE'>
                       OFFLINE TRADE
                    </MenuItem>
                    <MenuItem value=' E-COM'> E-COM</MenuItem>
                    <MenuItem value=' ONLINE PACKING'>
                       ONLINE PACKING
                    </MenuItem>
                    <MenuItem value=' ADMIN SALE'>
                       ADMIN SALE
                    </MenuItem>
                    <MenuItem value=''></MenuItem>
                    <MenuItem value=''></MenuItem>
                  </Select>
                </FormControl>
              </Grid>

              {/*  (C) */}
              <Grid item xs={12} sm={6}>
                <FormControl fullWidth>
                  <InputLabel></InputLabel>
                  <Select
                    name='location'
                    value={formData.location}
                    label=''
                    onChange={handleChange}
                    required
                  >
                    <MenuItem value=''>--  --</MenuItem>
                    {locationOptions.map((loc) => (
                      <MenuItem key={loc} value={loc}>
                        {loc}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>

              {/*  (D) */}
              <Grid item xs={12} sm={6}>
                <FormControl fullWidth>
                  <InputLabel></InputLabel>
                  <Select
                    name='approver'
                    value={formData.approver}
                    label=''
                    onChange={handleChange}
                  >
                    <MenuItem value=''>--  --</MenuItem>
                    {approverOptions.map((apv) => (
                      <MenuItem key={apv} value={apv}>
                        {apv}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>

              {/*  (E) */}
              <Grid item xs={12} sm={6}>
                <FormControl fullWidth>
                  <InputLabel></InputLabel>
                  <Select
                    name='assetName'
                    value={formData.assetName}
                    label=''
                    onChange={handleChange}
                  >
                    <MenuItem value=''>--  --</MenuItem>
                    {assetNameOptions.map((an) => (
                      <MenuItem key={an} value={an}>
                        {an}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>

              {/*  (F) */}
              <Grid item xs={12} sm={6}>
                <FormControl fullWidth>
                  <InputLabel></InputLabel>
                  <Select
                    name='assetCategory'
                    value={formData.assetCategory}
                    label=''
                    onChange={handleChange}
                  >
                    <MenuItem value=''>--  --</MenuItem>
                    <MenuItem value='PC'>PC</MenuItem>
                    <MenuItem value='Notebook'>Notebook</MenuItem>
                    <MenuItem value='Drone'>Drone</MenuItem>
                    <MenuItem value=''></MenuItem>
                    <MenuItem value='Computer'>Computer</MenuItem>
                    <MenuItem value=''></MenuItem>
                    <MenuItem value=''></MenuItem>
                    <MenuItem value=''></MenuItem>
                    <MenuItem value=''></MenuItem>
                    <MenuItem value=''></MenuItem>
                  </Select>
                </FormControl>
              </Grid>

              {/* Brand (G) */}
              <Grid item xs={12} sm={6}>
                <FormControl fullWidth>
                  <InputLabel>Brand</InputLabel>
                  <Select
                    name='brand'
                    value={formData.brand}
                    label='Brand'
                    onChange={handleChange}
                  >
                    <MenuItem value=''>--  Brand --</MenuItem>
                    {brandOptions.map((b) => (
                      <MenuItem key={b} value={b}>
                        {b}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
              </Grid>

              {/* Model/Type (H) */}
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  label='Model/Type'
                  name='modelType'
                  value={formData.modelType}
                  onChange={handleChange}
                />
              </Grid>

              {/* S/N Number (I) */}
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  label='S/N Number'
                  name='serialNumber'
                  value={formData.serialNumber}
                  onChange={handleChange}
                />
              </Grid>

              {/* Asset ID (J) */}
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  label='Asset ID'
                  name='assetId'
                  value={formData.assetId}
                  onChange={handleChange}
                />
              </Grid>

              {/* Barcode (K) */}
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  label='Barcode'
                  name='barcode'
                  value={formData.barcode}
                  onChange={handleChange}
                />
              </Grid>

              {/*  (L) */}
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  label=''
                  type='date'
                  name='receiveDate'
                  value={formData.receiveDate}
                  onChange={handleChange}
                  InputLabelProps={{ shrink: true }}
                />
              </Grid>

              {/*  (M) */}
              <Grid item xs={12} sm={6}>
                <FormControl fullWidth>
                  <InputLabel></InputLabel>
                  <Select
                    name='assetStatus'
                    value={formData.assetStatus}
                    label=''
                    onChange={handleChange}
                  >
                    <MenuItem value=''>--  --</MenuItem>
                    <MenuItem value=''></MenuItem>
                    <MenuItem value=''></MenuItem>
                    <MenuItem value=''></MenuItem>
                  </Select>
                </FormControl>
              </Grid>

              {/*  (O) */}
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  multiline
                  label=''
                  name='otherDetail'
                  value={formData.otherDetail}
                  onChange={handleChange}
                  placeholder=' ...'
                />
              </Grid>

              {/*  (P) */}
              <Grid item xs={12} sm={6}>
                <TextField
                  fullWidth
                  label=''
                  name='remark'
                  value={formData.remark}
                  onChange={handleChange}
                  placeholder=''
                />
              </Grid>

              {/*  (imageBase64 -> N) */}
              <Grid item xs={12} sm={6}>
                <Stack direction='row' spacing={2} alignItems='center'>
                  <Button variant='outlined' component='label'>
                    
                    <input
                      type='file'
                      accept='image/*'
                      hidden
                      onChange={handleImageChange}
                    />
                  </Button>
                  {formData.imageBase64 ? (
                    <Typography color='success.main'>
                       / 
                    </Typography>
                  ) : (
                    <Typography color='text.secondary'>
                      
                    </Typography>
                  )}
                </Stack>
              </Grid>

              {/*   */}
              <Grid item xs={12} sm={6}>
                <Button variant='outlined' onClick={() => setShowWebcam(true)}>
                  
                </Button>
                {formData.imageBase64 && (
                  <Typography color='success.main' sx={{ ml: 2 }}>
                    /
                  </Typography>
                )}
              </Grid>

              {/*  Submit */}
              <Grid item xs={12}>
                <Button variant='contained' type='submit'>
                  
                </Button>
              </Grid>
            </Grid>
          </Box>
        </CardContent>
      </Card>

      {/*  () */}
      {formData.imageBase64 && (
        <Card sx={{ mt: 2 }}>
          <CardContent sx={{ textAlign: "center" }}>
            <Typography variant='subtitle1'></Typography>
            <img
              src={formData.imageBase64}
              alt='Preview'
              style={{ maxWidth: 200, marginTop: 8 }}
            />
          </CardContent>
        </Card>
      )}

      {/* Overlay  */}
      {showWebcam && (
        <Box
          sx={{
            position: "fixed",
            top: 0,
            left: 0,
            width: "100vw",
            height: "100vh",
            bgcolor: "rgba(0,0,0,0.7)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            zIndex: 9999,
          }}
        >
          <Box sx={{ bgcolor: "#fff", p: 2, borderRadius: 1 }}>
            <WebcamCapture
              onCapture={handleCapture}
              onCancel={handleCancelWebcam}
            />
          </Box>
        </Box>
      )}
    </Container>
  );
}

export default AssetForm;
